import React, { Component } from "react";
import { Content, Main, OptionContent, Option } from "./SelectOption-css";

class SelectOption extends Component {
  state = {
    defaultOption: "女性",
    show: false
  };

  componentDidMount() {
    window.addEventListener("click", this.checking);
  }

  checking = e => {
    console.log(e.target.className);
    if (e.target.className.includes("main-option")) {
      return;
    } else {
      console.log("點到了");
      this.setState({
        show: false
      });
    }
  };

  showOptions = () => {
    this.setState({
      show: true
    });
  };

  handleChange = data => {
    this.setState({
      defaultOption: data,
      show: false
    });
  };

  render() {
    const { defaultOption, show } = this.state;
    const options = ["女性", "男性"];
    return (
      <Content>
        <Main onClick={this.showOptions} className="main-option">
          {defaultOption}
        </Main>
        <OptionContent show={show}>
          {options.map((data, index) => (
            <Option key={index} onClick={() => this.handleChange(data)}>
              {data}
            </Option>
          ))}
        </OptionContent>
      </Content>
    );
  }
}

export default SelectOption;
